<template>
  <div>
    <content-title
      title="屏蔽简历管理"
      tips="此处展示被屏蔽的简历，显示操作人员及操作时间。">
    </content-title>
    <content-c>
      <el-table
        :data="resumeList"
        style="width: 100%">
        <el-table-column
          prop="username"
          label="姓名">
        </el-table-column>
        <el-table-column
          prop="job.department.name"
          label="投递部门">
        </el-table-column>
        <el-table-column
          prop="form.value"
          label="来源">
        </el-table-column>
        <el-table-column
          prop="id"
          label="ID">
        </el-table-column>
        <el-table-column
          prop="operationUser.name"
          label="操作人员">
        </el-table-column>
        <el-table-column
          prop="operationTime"
          label="处理时间">
          <template slot-scope="scope">
            {{cptFormatDateTime(scope.row.operationTime)}}
          </template>
        </el-table-column>
        <el-table-column
          label="操作"
          width="300">
          <template slot-scope="scope">
            <button class="com-btn com-detail" @click="handleDetail(scope.row, scope.column, scope.$index)">查看</button>
            <button class="com-btn com-edit" @click="handleEmailDetail(scope.row)">邮件正文</button>
            <button class="com-btn com-del" @click="handleDel(scope.row)">删除</button>
          </template>
        </el-table-column>
      </el-table>

      <div class="com-page-box">
        <div class="com-pages">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="resumeListTotal">
          </el-pagination>
        </div>
      </div>
    </content-c>


    <!--查看-->
    <com-dialog
      :show="detailDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <div class="resume-detail">
        <p><span class="label">姓&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;名:</span>{{resumeData.username}}</p>
        <p><span class="label">投递部门:</span>{{resumeData.departmentName}}</p>
        <p><span class="label">来&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;源:</span>{{resumeData.form}}</p>
        <p><span
          class="label">I&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;D:</span>{{resumeData.id}}
        </p>
        <p><span class="label">操作人员:</span>{{resumeData.operationUserName}}</p>
        <p><span class="label">处理时间:</span>{{getTimeFormat(resumeData.operationTime)}}</p>
        <p><span class="label">屏蔽原因:</span>{{resumeData.remarks}}</p>
      </div>

      <div slot="title" class="com-dialog-title t-1">
        查看屏蔽简历
        <!--<div class="close x-color-1" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #64A5E7; border-color: #64A5E7;" type="primary"
                   @click="closeDialog">确定
        </el-button>
      </div>
    </com-dialog>

    <!--邮件正文-->
    <com-dialog
      :show="emailDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">

      <div class="resume-detail">
        <p><span class="label">标&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;题:</span>{{resumeData.mailTitle}}</p>
        <p><span
          class="label">时&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;间:</span>{{getTimeFormat(resumeData.mailSendTime)}}
        </p>
        <p><span class="label">收&nbsp;&nbsp;件&nbsp;&nbsp;人:</span>{{resumeData.mailId}}</p>

        <p class="com-dashed-line"></p>

        <p><span class="label label-block">
          正&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;文:</span>
          <a target="_blank" :href="serverBaseUrlCpt + resumeData.mailSavePath">点击查看</a>
        </p>

        <p class="com-dashed-line"></p>

        <p>
          <span class="label label-block">附&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;件:</span>
          <span class="label-block" v-for="f in resumeData.enclosures">
            {{f.name ? interceptStringCpt(f.name, 20, 5, 8) : ''}}
            &nbsp;&nbsp;<a target="_blank" :href="serverBaseUrlCpt + f.path">下载</a></span>
        </p>
      </div>

      <div slot="title" class="com-dialog-title t-2">
        邮件正文
        <!--<div class="close x-color-2" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #FEC71E; border-color: #FEC71E;" type="primary"
                   @click="closeDialog">确定
        </el-button>
      </div>
    </com-dialog>
  </div>
</template>
<script>
  import contentTitle from '../../componentsBody/contentTitle.vue'
  import contentC from '../../componentsBody/contentC.vue'
  import comDialog from '../../../components/toast/dialog.vue'
  import { mapState, mapActions, mapMutations } from 'vuex'
  import { formatDateTime, interceptString } from '../../../utils/utils'
  import { serverBaseUrl } from '../../../utils/baseConfig'

  export default {
    name: '',
    props: {},
    data () {
      return {
        currentPage: 1,
        pageSize: 10, // 每页条数
        pageSizes: [10, 20],
        detailDialogVisible: false,
        emailDialogVisible: false,
        resumeData: {
          username: '',
          departmentName: '',
          form: '',
          id: '',
          operationUserName: '',
          operationTime: ''
        },
        resumeStatusId: 2, // 屏蔽的简历ID
      }
    },
    computed: {
      ...mapState('resume', [
        'resumeList',
        'resumeListTotal',
        'delStatus',
      ]),
      serverBaseUrlCpt () {
        return serverBaseUrl
      },
    },
    watch: {
      delStatus (d) {
        if (d) {
          this.ac_resumeList({
            entity: {statusBig: {id: this.resumeStatusId}},
            pageNo: this.currentPage - 1,
            pageSize: this.pageSize
          }).then(() => {
            this.$message({
              type: 'success',
              message: '删除成功'
            })
          })
        }
      }
    },
    methods: {
      ...mapActions('resume', [
        'ac_resumeList',
        'ac_delResume',
      ]),
      ...mapMutations('resume', [
        'mut_resumeList',
      ]),
      handleDetail (row) {
        console.log(row)
        this.detailDialogVisible = true
        this.resumeData = {
          username: row.username,
          departmentName: row.job.department ? row.job.department.name : '', // 处理后台数据中没有job字段时报错
          form: row.form ? row.form.value : '',
          id: row.id,
          operationUserName: row.operationUser.name,
          operationTime: row.operationTime,
          remarks: row.remarks
        }
      },
      handleEmailDetail (row) {
        this.emailDialogVisible = true
        this.resumeData = row
      },
      handleDel (row) {
        this.$confirm('删除该简历, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.ac_delResume({
            resumeId: row.id
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      },
      handleSizeChange (size) {
        this.pageSize = size
        this.ac_resumeList({
          entity: {statusBig: {id: this.resumeStatusId}},
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      },
      handleCurrentChange (page) {
        this.currentPage = page
        this.ac_resumeList({
          entity: {statusBig: {id: this.resumeStatusId}},
          pageNo: page - 1,
          pageSize: this.pageSize
        })
      },
      cptFormatDateTime (Timestamp) {
        return formatDateTime(Timestamp)
      },
      closeDialog () {
        this.detailDialogVisible = false
        this.emailDialogVisible = false
        this.resumeData = {
          username: '',
          departmentName: '',
          form: '',
          id: '',
          operationUserName: '',
          operationTime: ''
        }
      },
      getTimeFormat (timeStamp) {
        return formatDateTime(timeStamp)
      },
      interceptStringCpt (Str, len, m, n) {
        return interceptString(Str, len, m, n)
      }
    },
    components: {
      contentTitle,
      contentC,
      comDialog,
    },
    beforeCreate () {},
    created () {
      this.ac_resumeList({
        entity: {statusBig: {id: this.resumeStatusId}},
        pageNo: 0,
        pageSize: this.pageSize
      })
    },
    beforeMount () {},
    mounted () {
    },
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {
      this.mut_resumeList([])
    },
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../../styles/common";

  .t-1 {
    background-color: #64A5E7;
  }

  .x-color-1 {
    color: #64A5E7;
  }
  .t-2 {
    background-color: #FDC61E;
  }

  .x-color-2 {
    color: #FDC61E;
  }
  .resume-detail {
    p {
      line-height: 50px;
    }
    .label {
      margin-right: 20px;
    }
  }
  .label-block {
    display: block;
  }
</style>



